body {
    margin: 0;
    padding: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.filter {
    z-index: 1;
    position: fixed;
    top: 5px;
    left: 5px;
}

.filter-field {
    width: 300px;
    padding: 5px;
}

ul {
    background: white;    
    overflow: auto;
    padding: 0px;
    margin: 0;
    max-height: 200px;
    overflow: auto;
}

li {
    height: 14px;
    font-size: 13px;
}

.container {
    cursor: pointer;
    padding: 5px;
}

.container:hover {
    background-color: mediumaquamarine;
    color: white;
    text-decoration: underline;
}

.button {
    margin-top: 5px;
    text-align: center;
    padding: 5px;
    background-color:maroon;
    border: 0px;
    cursor: pointer;   
    border-radius: 10px;
    color: white;
}


